
.overview {
    background: #42efae;
    padding: 3em 0;
}

.numscroller {
    font-size: 3em;
    font-weight: 600;
    color: #fff;
}

.stats-agileinfo h6 {
    letter-spacing: 3px;
    margin: .5em 0 0;
    font-size: 1.2em;
    color: #fff;
}


.articles{
    padding-top: 70px;
    padding-bottom: 40px;
}

h3.title-w3-agile {
    color: #000;
    font-size: 3em;
    letter-spacing: 1px;
    text-align: left;
    position: relative;
    margin-bottom: 1em;
    
}
h3.title-w3-agile:before {
    content: "";
    width: 59px;
    height: 2px;
    display: block;
    background: #000;
    position: absolute;
    left: 20px;
    bottom: -9px;
}
h3.title-w3-agile:after {
    content: "";
    width: 100px;
    height: 2px;
    display: block;
    background: #ff6b6b;
    position: absolute;
    top: 48px;
}

.w3gallery-grids a:hover{
    text-decoration: none;
}

.agile-figcaption {
    background: #000;
    text-align: center;
    padding: 1em 0;
	-webkit-transition: .5s all; 
    transition: .5s all; 
}
.agile-figcaption:hover {
    background: #ff6b6b;
	-webkit-transition: .5s all; 
    transition: .5s all; 
}
[class^='imghvr-'] > img,
[class*=' imghvr-'] > img {
	vertical-align: top;
	max-width: 100%;
	display: block;
}
[class^='imghvr-'] .agile-figcaption h4 {
    font-size: 1.5em;
    margin-bottom: .3em;
    font-weight: 600;
    color: #42efae;
}
[class^='imghvr-'] .agile-figcaption p{
    line-height: 2em;
    color: #fff;
    letter-spacing: 2px;
    margin: 0px;
}
[class^='imghvr-'] a,
[class*=' imghvr-'] a {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 1;
}
/*-- //gallery --*/  
.contact{
	background-color: #000;
	padding-top: 70px;
    padding-bottom: 40px;
}

.contact h3.title-w3-agile{
	color: #fff;
}

.contact h3.title-w3-agile:before{
	background-color: #fff;
}


.contact-agileits-w3layouts{
	background-image: url(/static/media/bg2.2c582a62.jpg);
	padding: 2em;
}

h4.sub {
    color: #fff;
    letter-spacing: 1px;
    font-size: 1.3em;
    padding-bottom: 10px;
}

.contact-agileits-w3layouts p {
    color: #eee;
    font-size: 14px;
    margin-top: 14px;
}

.contact-agileits-w3layouts input[type="text"], .contact-agileits-w3layouts input[type="email"], .contact-agileits-w3layouts textarea {
    border: none;
    border-bottom: 1px solid #fff;
    padding: 10px 15px;
    background: rgba(255, 255, 255, 0.41);
    color: #fff;
    letter-spacing: 1px;
    font-size: 1em;
    margin: 1em 0;
    width: 100%;
}

.contact-agileits-w3layouts .name,.contact-agileits-w3layouts .email{
	width: 50%!important;
	border-top: 1px solid #fff!important;
}

.contact-agileits-w3layouts .email{
	width: 50%!important;
	border-left: 1px solid #fff!important;
}

.contact-agileits-w3layouts textarea {
    min-height: 114px;
    resize: none;
}

::-webkit-input-placeholder{
	color:#fff;
}

.contact-agileits-w3layouts input[type="submit"] {
    outline: none;
    padding: 7px 40px;
    background:transparent;
    border: 1px solid #fff;
    color: #fff;
    font-size: 1em;
    margin: 1em 0 0;
    letter-spacing: 1px;
    transition: .5s ease-in-out;
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
    -ms-transition: .5s ease-in-out;
}

.contact-agileits-w3layouts input[type="submit"]:hover{
	border-color:#ef434a;
	transition:.5s ease-in-out;
	-webkit-transition:.5s ease-in-out;
	-moz-transition:.5s ease-in-out;
	-o-transition:.5s ease-in-out;
	-ms-transition:.5s ease-in-out;
}

.map-agileits-w3layouts iframe{
	border: 0;
}


.carouselText{
	position:absolute;
}


#video{
    background-image: url(/static/media/1.326e02be.png);
    background-attachment: fixed;
    position: relative;
    background-size: cover;
    width: 100%;
}

.mu-video-review-area {
    display: inline;
    float: left;
    padding: 150px 0;
    width: 100%;
}

.mu-heading-area {
    display: inline;
    float: left;
    text-align: center;
    padding: 0 10%;
    width: 100%;
}

.mu-video-review-area .mu-heading-title{
	margin-bottom: 25px;
}

.mu-heading-area p{
	font-size: 17px;
}

.mu-header-dot {
    border-radius: 0;
    height: 3px;
    width: 100px;
    display: inline-block;
    margin-bottom: 20px;
    background-color: #fff;
}

.mu-video-review-content {
    display: inline;
    float: left;
    margin-top: 70px;
    width: 100%;
}
